.hot {
  max-width: 1200px;
  margin: 120px auto 30px;
  /* background-color: white; */
  padding-top: 10px;
  .title {
    text-align: center;
    font-size: 36px;
    font-weight: bold;
    color: #4c4948;
    img {
      position: relative;
      top: -20px;
      width: 30%;
    }
  }
  .hot-block {
    ul {
      display: flex;
      > li {
        outline: 1px solid;
        margin-right: 20px;
        margin-bottom: 20px;
        width: 600px;
        .block-top {
          display: flex;
          justify-content: center;
          align-items: center;
          position: relative;
          img {
            position: absolute;
            width: 100px;
            top: -40px;
            left: -40px;
          }
          h2 {
            font-size: 28px;
            font-weight: bold;
            text-align: center;
            padding-top: 10px;
          }
        }
        .block-center {
          display: flex;
          padding: 20px;
          img {
            width: 300px;
          }
          .block-text {
            h2 {
              font-size: 28px;
              font-weight: bold;
              text-align: center;
              margin-bottom: 20px;
            }
            li {
              padding: 0 20px 10px;
              font-size: 20px;
              line-height: 1.4;
            }
          }
        }
        .block-bottom {
          display: flex;
          justify-content: space-around;
          .bottom-input {
            input {
              margin-right: 20px;
              background-color: #efefef;
              width: 100px;
              line-height: 30px;
              text-align: center;
              font-weight: bold;
              color: #4c4948;
              font-size: 16px;
              border-radius: 20px;
              outline: none;
              cursor: pointer;
              margin: 0 10px 10px;
            }
          }
          .bottom-text {
            display: flex;
            position: relative;
            h3 {
              font-size: 28px;
              font-weight: bold;
              margin-right: 10px;
            }
            h3:nth-child(2) {
              color: red;
            }
            h3:nth-child(2)::after {
              content: '$400';
              text-decoration: line-through;
              position: absolute;
              top: -30px;
              right: 50px;
              color: black;
              font-size: 20px;
            }
          }
        }
      }
    }
  }
}
